const URL = '/api/aurora/user';

export const getUser = async (
  id: string,
): Promise<{
  status: boolean;
  data: User | null;
  message: string;
}> => {
  try {
    if (!id) return { status: false, data: null, message: 'Áº∫Â∞ë ID Ë≥áË®ä' };
    const data: User = await fetch(`${URL}?id=${id}`).then((res) => res.json());
    return { status: true, data, message: 'success' };
  } catch (error) {
    console.error(error);
    return { status: false, data: null, message: 'ÁôºÁîü‰∏çÈ†êÊúüÁöÑÈåØË™§' };
  }
};

export const putUser = async (data: User) => {
  try {
    await fetch(URL, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(data),
    });
    return { status: true, message: 'success' };
  } catch (error) {
    console.error(error);
    return { status: false, message: 'ÁôºÁîü‰∏çÈ†êÊúüÁöÑÈåØË™§' };
  }
};

