const URL = '/api/aurora/items';

export const getItems = async (
  groupId?: string,
  email?: string,
  startDate?: string,
  endDate?: string,
): Promise<{
  status: boolean;
  data: SpendingRecord[];
  message: string;
}> => {
  try {
    const query = new URLSearchParams();
    if (groupId) {
      query.set('groupId', groupId);
    } else if (email) {
      query.set('email', email);
    } else {
      return { status: false, data: [], message: 'Áº∫Â∞ëÁæ§ÁµÑ ID Êàñ‰ø°ÁÆ±Ë≥áË®ä' };
    }
    if (startDate) query.set('startDate', startDate);
    if (endDate) query.set('endDate', endDate);
    const data = await fetch(`${URL}?${query}`).then((res) => res.json());
    return { status: true, data, message: 'success' };
  } catch (error) {
    console.error(error);
    return { status: false, data: [], message: 'ÁôºÁîü‰∏çÈ†êÊúüÁöÑÈåØË™§' };
  }
};

export const putItem = async (data: SpendingRecord) => {
  try {
    return await fetch(URL, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(data)
    });
  } catch (error) {
    console.error(error);
    return { status: false, message: 'ÁôºÁîü‰∏çÈ†êÊúüÁöÑÈåØË™§' };
  }
};

export const deleteItem = async (id: string) => {
  try {
    return await fetch(`${URL}?id=${id}`, { method: 'DELETE' });
  } catch (error) {
    console.error(error);
    return { status: false, message: 'ÁôºÁîü‰∏çÈ†êÊúüÁöÑÈåØË™§' };
  }
};
